:root {
  --background: #000000;
  --foreground: #ffffff;
  --neon-pink: #ff00ff;
  --neon-pink-accessible: #d600d6;
  --neon-cyan: #00ffff;
  --neon-cyan-accessible: #00b3b3;
  --true-black: #000000;
  --dark-bg: #050505;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background: #121224;
  color: var(--foreground);
  font-family: 'Courier New', monospace;
  background-attachment: fixed;
}
@keyframes neon-glow {
  0% {
    text-shadow: 0 0 5px var(--neon-cyan), 0 0 10px var(--neon-cyan), 0 0 15px var(--neon-cyan);
  }
  50% {
    text-shadow: 0 0 10px var(--neon-pink), 0 0 20px var(--neon-pink), 0 0 30px var(--neon-pink);
  }
  100% {
    text-shadow: 0 0 5px var(--neon-cyan), 0 0 10px var(--neon-cyan), 0 0 15px var(--neon-cyan);
  }
}
.neon-text {
  animation: neon-glow 2s ease-in-out infinite;
}
.neon-border {
  border: 3px solid var(--neon-cyan);
  box-shadow: 0 0 10px var(--neon-cyan), 0 0 20px var(--neon-cyan), inset 0 0 10px var(--neon-cyan);
  padding: 20px;
  margin: 20px 0;
  border-radius: 8px;
}
@keyframes flicker {
  0%,
  19.999%,
  22%,
  62.999%,
  64%,
  64.999%,
  70%,
  100% {
    opacity: 1;
  }
  20%,
  21.999%,
  63%,
  63.999%,
  65%,
  69.999% {
    opacity: 0.8;
  }
}
.flicker {
  animation: flicker 6s linear infinite;
}
::-webkit-scrollbar {
